* {
  box-sizing: border-box;

  &:after,
  &:before {
    box-sizing: border-box;
  }
}

a {
  text-decoration: none;

  &:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
  }
}

img {
  vertical-align: middle;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.container,
.wrapper {
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 768px) {
  .container, 
  .wrapper {
    width: 725px;
  }
}
@media (min-width: 992px) {
  .container, 
  .wrapper {
    width: 945px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }

  .wrapper {
    width: 100%;
    max-width: 1700px;
    margin: auto;
  }
}


.mainwrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

.footer {
  flex: 0 0 auto;
}