html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite,
del, dfn, em, img, ins,q, s, strike, sub, sup, tt, var,
b, u, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}


.container {
    @media @mobile {
        padding: 0 10px;
    }
    @media @tablet {
        padding: 0;
    }
}
a {
	outline: none;
    color: inherit;
}

li {
    list-style-type: none;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  
  &:hover {
    cursor: pointer;
  }
}

address {
  font-style: normal;
}


body {
    position: relative;
    margin: 0;
    min-height: 100vh;
    padding-right: 0 !important;
    color: #fff;
    font-size: @fs;
    line-height: @lh;
    font-weight: 400;
    font-family: @ff1;
    height: 100%;

    overflow-y: scroll;
    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        background-color: #F5F5F5;
    }
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-thumb {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: @breeze;
    }
}

*::selection {
    background-color: #6b6c70;
    color: #fff;
}


section {
    padding: 80px 0;

    @media @desktop-xl-2 {
        padding: 120px 0;
    }
}

main {
    padding-top: 100px;

    @media @desktop-xl {
        padding-top: 100px;
    }
    @media @desktop-xl-2 {
        padding-top: 160px;
    }
}

.btn {
    outline: none !important;
}


.section__title {
    display: table;
    margin: auto;
    position: relative;
    font-size: 22px;
    font-weight: 600;
    color: @black;
    text-align: center;

    @media @tablet {
        font-size: 28px;
    }
    @media @desktop {
        font-size: 36px;
        text-align: left;
    }
    @media @desktop-xl {
        font-size: 42px;
    }
    @media @desktop-xl-2 {
        font-size: 48px;
    }

    &:before {
        content: '';
        position: absolute;
        top: -120px;
        left: 50%;
        transform: translateX(-50%);
        width: 30px;
        height: 30px;
        background: @breezeLight;

        @media @desktop-xl {
            width: 40px;
            height: 40px;
        }
    }
    &:after {
        content: '';
        position: absolute;
        top: -60px;
        left: 50%;
        transform: translateX(-50%);
        width: 140px;
        height: 10px;
        background: @black;

        @media @desktop-xl {
            top: -40px;
            width: 180px;
            height: 20px;
        }
    }

