.remodal-overlay {

  // background: rgba( 160, 159, 158, 0.8) !important;
}

.remodal-first {
  padding-top: 95px;
  text-align: center;
  width: 376px;
  @media @mobile {
    max-width: 376px !important;
    height: auto;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
    background: #f9fbfc;
    border-radius: 10px;

  }
  @media @tablet {

    padding: 50px 15px;
    max-width: 489px !important;
    height: 487px;
    text-align: left;
    border-radius: 0;
    background: url('@{dir}form.png') no-repeat top center;
    padding: 120px 0 0 109px;

  }

  .remodal-close {
    @media @mobile {
      color: #000 !important;
    }
    @media @tablet {
      color: #fff !important;

    }
  }

  &:before {
    display: none;
  }
}

.btn-remodal {
  margin: 30px 0 0 15px;

}

.remodal-close {
  top: 12px !important;
  right: 15px !important;
  color: #000 !important;
  font-weight: 900;
  z-index: 10;

  @media @mobile {
    width: 10px !important;
    height: 16px !important;
  }
  @media @tablet {
    width: 35px !important;
    height: 30px !important;
  }

  &:before {
    @media @mobile {
      font-size: 30px !important;
      top: -7px !important;
      left: -12px !important;
    }
    @media @tablet {
      font-size: 50px !important;
      top: -0px !important;
      left: -0px !important;
    }
  }
}

.remodal-common {
  border-radius: 15px;
  background: #fff;
  padding: 30px 40px;
  @media @mobile {
    max-width: 500px !important;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;

  }
  @media @tablet {
    padding: 40px 20px;
    max-width: 450px !important;
    text-align: center;
  }

  .remodal-close {
    color: #000 !important;
  }
}

#name-error {
  font-size: 0 !important;
}

.input {
  position: relative;
}
.formBox {
  position: relative;
  max-width: 400px;
  margin: 20px auto;
  text-align: center;

  background-color: #f6f6f6;
  border-width: 0;
  border-color: transparent;
  border-radius: 4px;
  border-style: solid;
  box-sizing: border-box;
  display: inline-block !important;
  margin: 0;
  padding: 0;
  position: relative;

  form {
    padding: 30px;
    color: #000;
  }

  .form__title {
    padding-bottom: 25px;
    font-size: 22px;
    text-transform: uppercase;
  }

  .form__text {
    padding-bottom: 15px;
    font-size: 16px;
    line-height: 1.25;
  }

  .form__line {
    width: 90%;
    padding: 10px 15px;
    margin: 10px auto;
    outline: none;
    border: 1px solid #ccc;
    font-size: 18px;
    background: #fff;
  }

  .btn {
    margin-top: 25px;
    padding: 0 30px;
    line-height: 50px;
    font-weight: 900;
    text-transform: uppercase;
    border: none;
    background-color: #000;
    border-radius: 4px;
    color: #fff;
  }
}

.thanks__title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}

.thanks__text {
  display: block;
  padding-bottom: 15px;
  padding-left: 0;
  font-size: 18px;
  color: #000;
}
