.slick-prev,
.slick-next {
	position: absolute;
	display: block;
	top: 54%;
	bottom: 0;
	transform: none;
	width: 50px;
	height: 100%;
	transition: all .3s linear;
	z-index: 1000;
	font-size: 0;
	line-height: 0;
	outline: none;

	@media @tablet {
		transform: translateY(-50%);
	}

	&:before {
		display: block;
		font-size: 42px;
		font-family: 'fontello';
		color: @black;
		transition: all .3s linear;
		z-index: 20;
	}
	&:hover, &:focus {
		&:before {
			color: @breezeLight;
		}
	}
}
.slick-prev {
	left: 0px;
	@media @tablet {
		left: 30%;
	}	
}
.slick-next {
	right: 0px; 
	@media @tablet {
		right: 30%; 
	}
}
.slick-prev:before {
	content: '\e80a';
}
.slick-next:before {
	content: '\e809';
}
.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
	opacity: 1;
}
.slick-disabled {
	opacity: .35!important;
}
.slick-current, .slick-slide, .slick-active { 
	outline: none!important;
}



.slick-dots {
	display: inline-block!important;
	bottom: 30px;
	border-image: transparent;

	li {
		margin: 4px 8px;
		margin-bottom: 10px;

		width: 10px;
		height: 10px;

		button {
			background: #000;
			display: block;
			height: 10px;
			width: 10px;
			font-size: 0px;
			color: #000;
			border-radius: 50%;
			opacity: 0.75;
			&:before {
				font-size: 0;
				opacity: 0;
				display: none;
			}

		}
		&.slick-active button {
			background: @breezeLight;
			border-radius: 50%;
			opacity: 1;
		}
	}
}


.slick-current, .slick-slide, .slick-active { 
	outline: none!important;
}