@media @mobile-xl {}
@media @mobile-land {}
@media @tablet {}
@media @desktop {}
@media @desktop-xl {}
@media @desktop-xl-2 {}
@media @desktop-xll {}

.header {
  &__top {
    padding: 15px 0;
    background: #000;
    @media @desktop {
      padding: 20px 0;
    }
    @media @desktop-xl-2 {
      padding: 60px 0;
    }
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media @desktop {
      justify-content: flex-start;
    }
  }

  &__logo {
    img {
      max-width: 40px;
      @media @desktop {
        max-width: 62px;
      }
    }
    @media @desktop {
      margin-right: 150px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
    @media @desktop {
      padding-top: 0;
      flex-direction: row;
    }
  }

  &__item {
    margin-right: 20px;
  }

  &__link {
    display: block;
    padding: 15px;

    cursor: pointer;
    color: #fff;
    z-index: 10;
    @media @desktop {
      background: @breeze;
    }

    &:hover {
      background: #333;
    }
  }

  .wrapper {
    @media @desktop {
      width: 100%;
    }
  }
  &__slider {
    margin: 0;
    width: 100%;

    @media @tablet {
      margin: 30px auto 0 auto;
    }
    @media @desktop-xl-2 {
      margin: 60px auto 0;
    }
  }

  &__slide {
    position: relative;
    width: 100vw;
    min-height: 90vh;
    
    @media @tablet {
      min-height: 380px;
    }
    @media @desktop {
      min-height: 490px;
    }
    @media @desktop-xl-2 {
      min-height: 620px;
    }

    .slide__img img {
      position: absolute;
      width: 100vw;
      height: 100vh;

      @media @tablet {
        .img-responsive;
        min-height: 300px;
      }
      @media @desktop {
        min-height: 450px;
      }
    }

    .slide__content {
      margin: 80px auto 0;
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 10vh;
      max-width: 300px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      position: relative;
      z-index: 10;
      color: #000;
      text-align: center;

      @media @tablet {
        padding-top: 0;
        max-width: 350px;
        text-align: left;
      }
      @media @desktop {
        max-width: 600px;
      }
    }

    .slide__title {
      font-size: 20px;
      font-weight: 900;

      @media @tablet {
        font-size: 28px;
      }
      @media @desktop {
        font-size: 36px;
      }
      @media @desktop-xl {
        font-size: 42px;
      }
      @media @desktop-xl-2 {
        font-size: 48px;
      }
    }

    .slide__btnWrap {
      display: flex;
      justify-content: space-between;
      margin: 25px 0;
      width: 100%;

      @media @desktop {
        justify-content: flex-start;
        margin: 40px 0;
        max-width: 600px;
      }
    }

    .btn {
      min-width: 130px;
      line-height: 50px;
      background: #000;
      color: #fff;
      text-transform: uppercase;

      @media tablet {
        min-width: 150px;
        padding: 0 35px;
        margin-right: 30px;
      }
      @media @desktop {
        width: 200px;
        margin-right: 90px;
      }

      &:hover {
        background: #333;
      }
    }

    img {

      object-fit: cover;
      object-position: center;
    }
  }
}
